/*homepage*/

header{

  background: url(/static/media/hom.83325fab.jpg);
  height: 93.2vh;
  background-size: cover;
  -webkit-background-size:cover;
  position: relative;
  background-position: center center;

}

.banner-text{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;

}

.banner-text p {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: black;
  font-size: 30px;
}

.group-text h1{
  font-size: 60px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: #fff;

}

.group-text h1:hover{
  color: #000;
  box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
  -webkit-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
  -moz-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
}

.banner-text p:hover{
  color: rgb(248, 232, 6);
  box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
  -webkit-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
  -moz-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
}
/* Signup*/

main{
  background: url(/static/media/h12.90b41391.jfif);
  height: 110ch;
  background-repeat: no-repeat;
  background-size: cover;
}
.cont1{
  position: absolute;
  max-width: 800px;
  left: 50%;
  text-align: center;
  justify-content: center;
  transform: translate(-50%,10%);
  box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
  -webkit-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
  -moz-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
  background-image: linear-gradient(to left, #f73ced, #c452f5, #42afe2cc, #27cfe5);

}

.LeftCon{
  position: relative;
  border-radius: 25px;
  height: 100%;
  padding: 25px;
  padding-left: 50px;

}

.LeftCon h1{

  color:rgb(13, 10, 15);
  font-size: 35px;
  font-weight: 600;
  


}

.RightCon{
  position: relative;
  border: none;
  padding: 50px;
  height: auto;
  display:flex;
  justify-content: center;
  align-items: center;
}



.Signup-cont{
  position: relative;
  box-shadow:  0px 10px 40px -10px rgba(0,0,0,0.7);
  height: auto;
  width: 100%;
  background-image: linear-gradient(to left, #f73ced, #c452f5, #42afe2cc, #27cfe5);
  text-align: center;
  justify-content: center;
  background-color:white;
  -webkit-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
  -moz-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);

}

.LeftCon .text-Box{
  width: 230px;
  border: none;
  border-radius:  15px ;
  padding: 10px;
  font-size: 15px;
  margin-top: 1%;
  font-weight: 490;
  box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
  -webkit-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
  -moz-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
   
}

.LeftCon .stylebtn{
  background-image: linear-gradient(to left, #9613d3, #9e18d8, #a61edc, #ae22e1, #b627e5);
  color: #fff;
  width: 130px;
  border: none;
  margin-top: 5%;
