  padding: 10px;
  box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
  -webkit-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
  -moz-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
}
.header{
  font-size: 20px;
  font-family: 'Times New Roman', Times, serif;
   color: rgb(26, 24, 24);
   
}

.RightCon img{
  border: none;
  border-radius: 10px;
  text-align: center;
  margin-top: 19%;
  justify-content: center;
 padding: 30px;
 box-shadow: 0px 10px 40px -10px rgba(238, 232, 232, 0.973);
 -webkit-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.7);
 -moz-box-shadow: 0px 10px 40px -10px rgba(0,0,0,0.2);
}

.LeftCon .stylebtn:hover{
  background-image: linear-gradient( #2265e1, #a27aec);

}

.LeftCon .text-Box:focus{
  outline: none;
}

.SignupForm{
  position: relative;
  margin-top: 50px;
}





