*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "poppins", sans-serif;
}

.container{
    background-color: rgb(250, 249, 249);

 width: 100%;
 height: auto;
}

.Contact{
    display: flex;
    justify-content: center;
    align-items: center;
      
}

.Con_form .text-box:hover{
 color: rgb(20, 13, 13);
 box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
 -webkit-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
 -moz-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);

}

.cont_info{
    color: rgb(12, 3, 10);
    align-items: center;
    text-align: center;
    line-height: 40px;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 1px;
    text-decoration: underline;
    font-family: Arial, Helvetica, sans-serif;
  
}

.cont_info:hover{
    color: rgb(19, 13, 13);
    box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
    -webkit-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);
    -moz-box-shadow: 0px 10px 50px -10px rgba(0,0,0,0.9);

}
.Contact_form{
    max-width: 700px;
    

    
}



.Contact_form h2{
    color: black;
    text-align: center;
    font-size: 35px;
    text-transform: uppercase;
      margin-bottom: 30px; 
      

}
.Contact_form .text-box{
    background: #000;
    color: #fff;
    border: none;
    width: 100%;
    height: 50px;
    padding: 12px;
    font-size: 15px;
    border-radius: 5px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
    margin-bottom: 20px;
    opacity: 0.9;

}



.Contact_form textarea{
    background: #000;
    color: #fff;
    border: none;
    width: 100%;
    padding: 12px;
    font-size:15px;
    min-height: 200px;
    max-height: 400px;
    resize: vertical;
    border-radius: 5px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
    opacity: 0.9;
    margin-bottom: 20px;


}

.Contact_form .send_bt{
    float: right;
    background: #2E94E3;
    color: #fff;
    border: none;
    width: 120px;
    height: 40px;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 5%;
    letter-spacing: 2px;
    border-radius: 5px;
    
}
.Contact_form .send_bt:hover{
    background-color: green;
}



@media screen and (max-width: 950px){
    .Contact{
        flex-direction: column;
    }

.Cont_Info .Contact_form{
    margin: 30px 50px;
}
.Contact_form h2{
    font-size: 30px;
}





}

    

